import React, { useState, useEffect } from "react";

export default function AddVideos() {
    const [likesvideos, setLikesvideos] = useState([]);
    const [resto, setResto] = useState([]);
    const [link, setLink] = useState("");
    const [url, setUrl] = useState("");
    const handleOnChange = (event) => {
        const { name, value } = event.target;
        setLink(value);
    };
    const to10 = () => {
        setLikesvideos((oldArray) => [...oldArray, link]);
        setLink("");
    };
    const palresto = () => {
        setResto((oldArray) => [...oldArray, link]);
        setLink("");
    };
    useEffect(() => {
        console.log("cambio");
        setUrl(encodeURIComponent(JSON.stringify({ likesvideos, resto })));
    }, [likesvideos, resto]);
    useEffect(() => {
        console.log(window.location.origin + "/dvd/?url=" + url);
        console.log(url);
    }, [url]);

    return (
        <div style={{ color: "black" }}>
            Link
            <input type="text" value={link} onChange={handleOnChange}></input>
            <button onClick={to10}>pa' 10likes</button>
            <button onClick={palresto}>pal' resto</button>
            <h1>hay {likesvideos.length} para likes</h1>
            <h1>hay {resto.length} para el resto</h1>
            <button
                onClick={() => {
                    navigator.clipboard.writeText(
                        window.location.origin + "/dvd/?url=" + url,
                    );
                }}
            >
                Click para copiar
            </button>
        </div>
    );
}
